import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'filter'
})
export class FilterPipe implements PipeTransform {

  transform(value: any, searchUser: any): any {
    return value.filter((search) => {
      console.log(search);
      return search.profile_detail.doc_first_name.toLowerCase().indexOf(searchUser.toLowerCase()) > -1
    });
  }

}