    console.log(segments);
    const username = segments[2];
    const token = localStorage.getItem("token-" + username);
    console.log(token);

    if (token) {
      this.router.navigateByUrl(`/home/${username}/dashboard`);
      return false;
    } else {
     // this.router.navigateByUrl(`/home/${username}`);
      return true;
    }
