import { ChangeDetectorRef, Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { Observable } from 'rxjs';
import { BaseHttpService } from './services/base-http.service';
import { DoctorService } from './services/doctor-service/doctor.service';
import { MessageService } from './services/message.service';
import { MediaControlService } from './shared/UI_service/media-control.service';
@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.less']
})
export class AppComponent implements OnInit {
  title = 'digiDoctor';
  isCollapsed = true;
  hamburger: boolean;
  side_page_nav: boolean;
  isport: boolean;
  haveChatBox: boolean = true;
  loading = false;
  loading$: Observable<boolean>;
  username: string;

  constructor(private _viewMedia: MediaControlService, public router: Router, private httpService: BaseHttpService,
    private cdRef: ChangeDetectorRef, private msg: MessageService, private route: ActivatedRoute, private doctorService: DoctorService
  ) { }
  ngOnInit(): void {


    this.loading$ = this.httpService.loading$;

    this.loading$.subscribe((isLoading) => {
      this.loading = isLoading;
    });

    this.msg.title.subscribe(title => {
      this.title = title;
    });
  }
  // ngAfterContentChecked(): void {
  //   this.hamburger = this._viewMedia.mediaPort()
  //   this.side_page_nav = this._viewMedia.mediaPort()
  //   this.isport = this._viewMedia.mediaPort()
  // }
  // chatBox(param) {
  //   this.haveChatBox = param;
  // }
  // scrollTo(section) {
  //   document.querySelector('#' + section)
  //     .scrollIntoView();
  // }

  ngAfterContentChecked() {
    this.cdRef.detectChanges()
  }
}
