import { Location } from '@angular/common';
import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { GetDrNameService } from '../../UI_service/get-dr-name.service';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.less'],
  providers: [GetDrNameService]
})
export class HeaderComponent implements OnInit {
  @Input() public headerTitle: string;
  @Input() public isFixed: boolean;
  @Input() public titleAlign: string;

  constructor(private _router: Router, private _dr: GetDrNameService, private _location: Location) { }

  ngOnInit(): void {
  }
  back() {
    console.log('woking');
    this._location.back()
  }
  headingAlign() {
    if (this.titleAlign == 'left') {
      return { 'text-align': 'left' }
    } else if (this.titleAlign == 'right') {
      return { 'text-align': 'right' }
    } else {
      return { 'text-align': 'center' }
    }
  }
}
