import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HeaderComponent } from './header/header.component';
import { FooterComponent } from './footer/footer.component';
import { SideNavComponent } from './side-nav/side-nav.component';
import { CalendarComponent } from './calendar/calendar.component';
import { ZorroAntComponentsModule } from '../themes/zorro-ant-components.module';
import { StatusCardComponent } from './status-card/status-card.component';

import { ScrollingModule } from '@angular/cdk/scrolling';
import { LanguageComponent } from './footer/page/language/language.component';
import { DivScrollService } from '../UI_service/div-scroll.service';
import { ContainerComponent } from './container/container.component';
import { TruncatePipe } from './pipes/truncate.pipe';
import { FilterPipe } from './pipes//filter.pipe';

const allCompanents = [
  HeaderComponent, FooterComponent, SideNavComponent, LanguageComponent, CalendarComponent, StatusCardComponent, ContainerComponent, FilterPipe, TruncatePipe
]

@NgModule({
  declarations: [allCompanents],
  imports: [
    CommonModule,
    ZorroAntComponentsModule,
    ScrollingModule
  ],
  providers: [DivScrollService],
  exports: [allCompanents]
})
export class ShareComponentsModule { }
