import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CustomPreLoadingService } from './shared/UI_service/custom-pre-loading.service';
import { PageNotFoundComponent } from './page-not-found/page-not-found.component';
import { HomeSearchComponent } from './home-search/home-search.component';
import { BlankComponent } from './home-search/blank/blank.component';

const routes: Routes = [
  // Home Search module
  { path: '', component: BlankComponent },
  { path: 'home', loadChildren: () => import('./doctor-portal/doctor-portal.module').then(m => m.DoctorPortalModule) },
  // doctor Portal module
  { path: 'doctorPortal', data: { preload: true }, loadChildren: () => import('./doctor-portal/doctor-portal.module').then(m => m.DoctorPortalModule) },
  // PatientDashboardModule
  { path: 'PatientDashboard', data: { preload: false }, loadChildren: () => import('./patient-dashboard/patient-dashboard.module').then(m => m.PatientDashboardModule) },
  // PageNotFoundComponent PatientDashboardModule
  { path: '**', component: PageNotFoundComponent },
  { path: '', component: PageNotFoundComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    preloadingStrategy: CustomPreLoadingService
  })],
  exports: [RouterModule],
  providers: [CustomPreLoadingService]
})
export class AppRoutingModule { }
