import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { GetDrNameService } from '../shared/UI_service/get-dr-name.service';
import { DoctorSearchService } from './service/doctor-search.service';

@Component({
  selector: 'app-home-search',
  templateUrl: './home-search.component.html',
  styleUrls: ['./home-search.component.less'],
  providers: [GetDrNameService]
})
export class HomeSearchComponent implements OnInit {

  UserSerchNow: string = null;
  all_Doctor: Array<any>;
  tested;
  ddd = 'you can search doctor by name by pin code'
  constructor(private _serviceAllDoctor: DoctorSearchService, private _dr: GetDrNameService, private _router: Router) {
    this.all_Doctor = new Array<any>()
  }
  searchResult(pramram) {
    let username = pramram.profile_detail.username == undefined  ? 'vijayijardar1' : pramram.profile_detail.username;
    console.log(username);
    this._router.navigate([`/home/${username}`])
  }

  ngOnInit() {
    this.C_Get_all_Doctor()
  }
  C_Get_all_Doctor() {
    this._serviceAllDoctor.S_Get_all_Doctor({ "limit": 17, "offset": 0, "firstName": "vijay", "lastName": "" }).subscribe(res => {
      this.all_Doctor = res['data'];
      this.tested = this.all_Doctor['doctors'];
    })
  }
}
