import { Component, Input, OnInit } from '@angular/core';
import { DynamicTitleService } from '../../UI_service/dynamic-title.service';

@Component({
  selector: 'app-container',
  templateUrl: './container.component.html',
  styleUrls: ['./container.component.less'],
  providers: [DynamicTitleService]
})
export class ContainerComponent implements OnInit {
  @Input() public tabTitle: string;
  @Input() public metaName: string;
  @Input() public metaContent: string;

  constructor(private _dynamicTitle: DynamicTitleService) { }

  ngOnInit(): void {
    this.containerMetaData()
  }
  containerMetaData() {
    if (this.metaName && this.metaContent) {
      this._dynamicTitle.rootMetaData(this.metaName, this.metaContent)
    } if (this.tabTitle) {
      this._dynamicTitle.setPageTitle(this.tabTitle)
    }
    else {
      return null;
    }
  }
}
