import { Injectable } from '@angular/core';
import { ScrollToService, ScrollToConfigOptions } from '@nicky-lenaers/ngx-scroll-to';

@Injectable({
  providedIn: 'root'
})
export class DivScrollService {

  constructor(private scrollToService: ScrollToService) { }
  public sectionName = [
    {
      section: 'section1',
      tooltip: 'Home',
      iconName: 'home.svg'
    },
    {
      section: 'section2',
      tooltip: 'Appointment',
      iconName: 'calendar.svg'
    },
    {
      section: 'section3',
      tooltip: 'Overview',
      iconName: 'address.svg'
    },
    {
      section: 'section4',
      tooltip: 'Treatement',
      iconName: 'accessible.svg'
    },
    {
      section: 'section5',
      tooltip: 'Gallery',
      iconName: 'images.svg'
    },
    {
      section: 'section6',
      tooltip: 'Testimonial',
      iconName: 'time.svg'
    },
    {
      section: 'section7',
      tooltip: 'Clinic Location',
      iconName: 'location.svg'
    },
    {
      section: 'section8',
      tooltip: 'Contact',
      iconName: 'call.svg'
    }
  ]
  triggerScrollTo(idName) {
    const config: ScrollToConfigOptions = {
      target: idName
    };
    this.scrollToService.scrollTo(config);
  }
}
