import { Input } from '@angular/core';
import { Component, OnInit } from '@angular/core';
import { Doctor } from '../../../interface/doctor';
import { Router } from '@angular/router';
import { MediaControlService } from '../../../shared/UI_service/media-control.service'
import { DivScrollService } from '../../UI_service/div-scroll.service';
@Component({
  selector: 'app-footer',
  templateUrl: './footer.component.html',
  styleUrls: ['./footer.component.less'],
  providers: [MediaControlService]
})
export class FooterComponent implements OnInit {
  thisDevice: boolean;
  @Input() doctor: Doctor;

  constructor(private _viewMedia: MediaControlService, private _router: Router, private _divScroll: DivScrollService) { }

  ngOnInit(): void {
  }
  isVisibleTop = false;
  isVisibleMiddle = false;

  public selectModal: string;
  showModalMiddle(param): void {
    this.isVisibleMiddle = true;
    this.selectModal = param;
    this.isVisibleTop = true;
  }

  handleOkTop(): void {
    this.isVisibleTop = false;
  }

  handleCancelTop(): void {
    this.isVisibleTop = false;
  }

  handleOkMiddle(): void {
    console.log('click ok');
    this.isVisibleMiddle = false;
  }

  handleCancelMiddle(): void {
    this.isVisibleMiddle = false;
  }
  ngAfterContentChecked(): void {
    this.thisDevice = this._viewMedia.mediaPort();
  }

  navRouterLink(param) {
    let dyUrl = `home/vijayijardar/${param}`
    this._router.navigate([`${dyUrl}`])
  }

  // ________ page scroll Spy ____________
  public DivName = this._divScroll.sectionName;
  scroll(divId: string) {
    this._divScroll.triggerScrollTo(divId)
  }
}